import { Entypo } from '@expo/vector-icons';
import { Divider, Layout, Text, useTheme } from '@ui-kitten/components';
import { FlatList, TouchableOpacity } from 'react-native';
import { useAppSelector } from '../../app/hooks';

type AddedClassesProps = {
  setAddedClassesVisible: (visible: boolean) => void;
  addedClasses: { [masterCourseCode: string]: string[] };
  handleRemoveClass: (courseCode: string, masterCourseCode: string) => void;
  darkMode: boolean;
};

export default function AddedClasses({
  addedClasses,
  handleRemoveClass,
  darkMode
}: AddedClassesProps) {
  const theme = useTheme();
  const allClassesList = useAppSelector(
    (state) => state.appData.classPages.classes
  );

  const addedClassesList = [];
  Object.entries(addedClasses).forEach(([masterCourseCode, courseCodes]) => {
    courseCodes.forEach((courseCode) => {
      const courseName = allClassesList[masterCourseCode].courseName;
      addedClassesList.push([masterCourseCode, courseCode, courseName]);
    });
  });

  const renderItem = ({ item: [masterCourseCode, courseCode, courseName] }) => {
    return (
      <Layout
        style={{
          flexDirection: 'row',
          justifyContent: 'space-between',
          alignItems: 'center',
          paddingVertical: 7,
          backgroundColor: darkMode
            ? theme['color-basic-900']
            : theme['color-basic-100']
        }}
      >
        <Layout style={{ backgroundColor: 'transparent' }}>
          <Text category="s1">{courseName}</Text>
          <Text category="p2">{courseCode}</Text>
        </Layout>

        <TouchableOpacity
          onPress={() => handleRemoveClass(courseCode, masterCourseCode)}
        >
          <Entypo name="cross" size={28} color="red" />
        </TouchableOpacity>
      </Layout>
    );
  };

  return (
    <Layout
      style={{
        flex: 1,
        paddingBottom: 10,
        backgroundColor: darkMode
          ? theme['color-basic-900']
          : theme['color-basic-100']
      }}
    >
      <FlatList
        contentContainerStyle={{ width: 300 }}
        ListEmptyComponent={
          <Text>
            No classes yet... Click the{' '}
            <Text style={{ fontWeight: 'bold' }}>+</Text> next to a class to see
            it here
          </Text>
        }
        ItemSeparatorComponent={() => <Divider />}
        data={addedClassesList}
        renderItem={renderItem}
      />
    </Layout>
  );
}
