import { createSlice, PayloadAction } from '@reduxjs/toolkit';

import type { RootState } from '../../app/store';
import { Class, Filters } from '../../app/types';
import { classPagesApi } from '../api/classPagesApi';

// Define a type for the slice state
interface ClassPagesState {
  classes: { [masterCourseCode: string]: Class };
  filteredClasses: { [masterCourseCode: string]: Class };
  renderedClasses: { [masterCourseCode: string]: Class };
  currentSection: string;
  availableSections: string[];
  searchTerm: string;
  selectedClass: any;
  page: number;
  filters: Filters;
  isHMC: boolean;
  hasSelectedElectiveGen: boolean;
  suggestedClasses: { [masterCourseCode: string]: Class };
}

// Define the initial state using that type
const initialState: ClassPagesState = {
  classes: {},
  filteredClasses: {},
  renderedClasses: {},
  currentSection: '',
  availableSections: [],
  selectedClass: {},
  searchTerm: '',
  page: 1,
  isHMC: false,
  filters: {
    colleges: {
      HM: true,
      CM: true,
      PO: true,
      PZ: true,
      SC: true
    }
  },
  hasSelectedElectiveGen: false,
  suggestedClasses: {}
};

export const classPagesSlice = createSlice({
  name: 'classPages',
  initialState,
  reducers: {
    reset: () => initialState,
    currentSectionUpdated: (state, action: PayloadAction<string>) => {
      state.currentSection = action.payload;
    },
    availableSectionsListUpdated: (state, action: PayloadAction<string[]>) => {
      state.availableSections = action.payload;
    },
    endOfListReached: (state) => {
      state.page++;
    },
    resetClasses: (state) => {
      state.page = 1;
      state.searchTerm = '';

      classPagesSlice.caseReducers.searchResultsFiltered(state);
    },
    suggestedClassesUpdated: (
      state,
      action: PayloadAction<{ [masterCourseCode: string]: Class }>
    ) => {
      state.suggestedClasses = action.payload;
    },
    suggestedClassesDimissed: (state) => {
      state.suggestedClasses = {};
    },
    electiveGeneratorSelected: (state) => {
      state.hasSelectedElectiveGen = true;
    },
    classSelected: (state, action: PayloadAction<string>) => {
      state.selectedClass = state.classes[action.payload];
    },
    selectedClassCleared: (state) => {
      state.selectedClass = {};
    },
    filtersSaved: (state, action: PayloadAction<Filters>) => {
      state.filters = action.payload;
      classPagesSlice.caseReducers.searchResultsFiltered(state);
    },
    isHMCUpdated: (state, action: PayloadAction<boolean>) => {
      state.isHMC = action.payload;
    },
    filtersReset: (state) => {
      state.filters = initialState.filters;

      classPagesSlice.caseReducers.searchResultsFiltered(state);
    },
    searchResultsFiltered: (state) => {
      // Update the renderedClasses object with the new filters
      let newResultsArray = Object.entries(state.classes);

      newResultsArray = newResultsArray.filter(([masterCourseCode, _]) => {
        const collegeAbr = masterCourseCode.substring(
          masterCourseCode.length - 2,
          masterCourseCode.length
        );

        for (const [college, isCollegeFiltered] of Object.entries(
          state.filters.colleges
        )) {
          if (collegeAbr === college) {
            return isCollegeFiltered;
          }
        }
      });

      state.filteredClasses = Object.fromEntries(newResultsArray);
      state.renderedClasses = { ...state.filteredClasses };
    },
    searchTermUpdated: (state, action: PayloadAction<string>) => {
      let newResultsArray = Object.entries(state.filteredClasses);
      state.searchTerm = action.payload;
      state.page = 1;

      if (state.searchTerm === '') {
        classPagesSlice.caseReducers.resetClasses(state);
      } else {
        const searchTerm = matchDepartmentName(state.searchTerm);

        // Check for courseCode shortcuts
        const searchCourseCode = findSearchTermShortcuts(searchTerm);

        const searchTerms = searchTerm.split(' ');

        newResultsArray = newResultsArray.filter(
          ([_, { courseName, times, masterCourseCode }]) => filterBySearchTerm(
            courseName,
            times,
            masterCourseCode,
            searchCourseCode,
            searchCourseCode === state.searchTerm ? searchTerms : []
          )
        );

        const newResultsObject = Object.fromEntries(newResultsArray);

        state.renderedClasses = { ...newResultsObject };
      }
    }
  },
  extraReducers: (builder) => {
    builder.addMatcher(
      classPagesApi.endpoints.getAllClasses.matchFulfilled,
      (state, { payload }) => {
        state.classes = payload;
        state.filteredClasses = payload;
        state.renderedClasses = payload;
      }
    );
  }
});

// Helper functions

const findSearchTermShortcuts = (searchPhrase: string) => {
  return searchPhrase
    .replace(/(csci *|cs *)(\d+)/i, (match, p1, p2) => `csci ${p2}`)
    .replace(
      /((e *)|(eng *)|(engr *))(\d+)/i,
      (match, p1, p2, p3, p4, p5) => `engr ${p5}`
    )
    .replace(/(bio *|biol *)(\d+)/i, (match, p1, p2) => `biol ${p2}`)
    .replace(
      // pad 0s for courseCode search
      /([a-z]+ *)(\d+)/i,
      (match, p1, p2) => p1 + p2.padStart(3, '0')
    );
};

// convert full department name search phrase to the department code
const matchDepartmentName = (searchPhrase: string) => {
  return searchPhrase
    .replace(/(computer science)/i, 'csci')
    .replace(/(engineering)/i, 'engr')
    .replace(/(biology)/i, 'biol');
};

const filterBySearchTerm = (
  courseName: string,
  times: any[],
  masterCourseCode: string,
  searchCourseCode?: string,
  searchTerms?: string[]
) => {
  const coursePhrase = `${masterCourseCode} ${courseName} ${
    times[0]?.professorName ? times[0].professorName : ''
  }`;

  // Return true if any of the searchCourseCodes are found in the masterCourseCode
  if (
    masterCourseCode
      .trim()
      .replace(/\s+/g, ' ')
      .includes(searchCourseCode.toUpperCase().trim().replace(/\s+/g, ' '))
  ) {
    return true;
  }

  // Return true if all of the searchPhrases are found in the coursePhrase
  for (const searchTerm of searchTerms) {
    if (
      !coursePhrase
        .toUpperCase()
        .trim()
        .replace(/\s+/g, ' ')
        .includes(searchTerm.toUpperCase().trim().replace(/\s+/g, ' '))
    ) {
      return false;
    }
  }

  return searchTerms.length > 0;
};

const filterByCollege = (filters, [className, _]) => {
  const college = className.split(' ').at(-1);
  return filters.colleges.includes(college);
};

// Selectors
export const selectClassesList = (state: RootState) => state.appData.classPages.classes;

export const selectRenderedClassesList = (state: RootState) => state.appData.classPages.renderedClasses;

// Actions
export const {
  currentSectionUpdated,
  endOfListReached,
  searchTermUpdated,
  resetClasses,
  classSelected,
  selectedClassCleared,
  reset,
  filtersSaved,
  filtersReset,
  isHMCUpdated,
  availableSectionsListUpdated,
  electiveGeneratorSelected,
  suggestedClassesUpdated,
  suggestedClassesDimissed
} = classPagesSlice.actions;

export default classPagesSlice.reducer;
