import {
  Button, Divider, Layout, Text, Toggle
} from '@ui-kitten/components';
import Constants from 'expo-constants';
import React, {useEffect, useState} from 'react';
import { useColorScheme } from 'react-native';

import {
  deviceDarkModeSet,
  reset as resetAppData,
  selectDarkMode,
  selectIsGuest,
  selectUserKey,
  useDeviceColorSchemeSet
} from '../../app/appDataSlice';
import { useAppDispatch, useAppSelector } from '../../app/hooks';
import ModalContainer from '../../components/ModalContainer';
import WrapItems from '../../components/WrapItems';
import { apiSlice } from '../api/apiSlice';
import { useDeleteAccountMutation, useGetUserInfoQuery } from '../api/usersApi';
import { reset as resetClassPages } from '../classPages/classPagesSlice';
import { reset as resetDiscover } from '../discover/discoverSlice';
import {base_URL} from "../../constants/admin";

export default function SettingsPage({ navigation }) {
  const darkMode = useAppSelector(selectDarkMode);
  const useDeviceColorScheme = useAppSelector(
    (state) => state.appData.secure.useDeviceColorScheme
  );
  const userKey = useAppSelector(selectUserKey);
  const isGuest = useAppSelector(selectIsGuest);
  const colorScheme = useColorScheme();
  const { data: userData } = useGetUserInfoQuery(userKey, {
    skip: !userKey
  });
  const dispatch = useAppDispatch();
  const [showGuestModal, setShowGuestModal] = useState(false);
  const [deleteAccount, { isLoading }] = useDeleteAccountMutation();
  const [ settingsData, setSettingsData ] = useState(userData.settings);

  const logOut = () => {
    dispatch(resetClassPages());
    dispatch(resetDiscover());
    dispatch(resetAppData());
    dispatch(apiSlice.util.resetApiState());
  };

  const handleDeleteAccount = async () => {
    try {
      await deleteAccount({ userKey, userName: userData.userName }).unwrap();
      logOut();
    } catch (e) {
      console.warn('Error deleting account: ', e);
    }
  };

  const handleUseDeviceSettingToggled = (checked: boolean) => {
    dispatch(useDeviceColorSchemeSet(checked));
    if (checked) {
      dispatch(deviceDarkModeSet(colorScheme === 'dark'));
    }
  };

  const handleDarkModeToggled = (checked: boolean) => {
    dispatch(useDeviceColorSchemeSet(false));
    dispatch(deviceDarkModeSet(checked));
  };

    useEffect(() => {
        getMe()
    }, []);

  function handleSettingsChange(settingName, value) {
      const body = {
          settings: {}
      }
      body.settings[settingName] = value
      fetch(base_URL + '/users/' + userData.userName, {
          method: 'PATCH',
          headers: {
              userKey,
              'content-type': 'application/json'
          },
          body: JSON.stringify(body)
      }).then(() => {})
      const newSettings = settingsData
      newSettings[settingName] = value
      setSettingsData(newSettings)
  }

  function getMe() {
      fetch(base_URL + '/users/me', {
          method: 'GET',
          headers: {
              userKey,
              'content-type': 'application/json'
          }
      }).then(data => {
          data.json().then((user) => {
            setSettingsData(user.settings);
          });
      })
  }

  return (
    <Layout style={{ flex: 1 }}>
      <Layout
        style={{
          flexDirection: 'row',
          marginHorizontal: 20,
          justifyContent: 'space-between',
          alignItems: 'center',
          flex: 1
        }}
      >
        <Layout>
          <Text category="h2">
            {userData?.realFirstName}
            {' '}
            {userData?.realLastName}
          </Text>
          <Text category="h6" status="success">
            @
            {userData?.userName}
          </Text>
        </Layout>
        <Layout>
          <Button
            onPress={() => {
              if (isGuest) {
                setShowGuestModal(true);
              } else {
                navigation.navigate('EditUser');
              }
            }}
          >
            Edit
          </Button>
        </Layout>
      </Layout>
      {showGuestModal && (
        <ModalContainer
          title="Wait!"
          children={(
            <Text style={{ paddingHorizontal: 30 }}>
              This feature is locked to our Guest Users. Please create a free
              account with us to take advantage this feature.
            </Text>
          )}
          backDropVisible={showGuestModal}
          setBackDropVisible={setShowGuestModal}
        />
      )}

      <Divider />

      <Layout
        style={{
          flex: 5,
          justifyContent: 'space-between',
          marginHorizontal: 20
        }}
      >
        <Layout>
          <Text category="h6" style={{ textAlign: 'center', marginTop: 20 }}>
            Dark Mode
          </Text>
          <Layout
            style={{
              marginTop: 20,
              flexDirection: 'row',
              justifyContent: 'space-between'
            }}
          >
            <Text>Use Device Setting</Text>
            <Toggle
              onChange={(checked) => handleUseDeviceSettingToggled(checked)}
              checked={useDeviceColorScheme}
            />
          </Layout>
          <Layout
            style={{
              marginTop: 20,
              flexDirection: 'row',
              justifyContent: 'space-between',
              opacity: useDeviceColorScheme ? 0.4 : 1
            }}
          >
            <Text>Dark Mode</Text>
            <Toggle
              onChange={(checked) => handleDarkModeToggled(checked)}
              checked={darkMode}
            />
          </Layout>

            <Text category="h6" style={{ textAlign: 'center', marginTop: 20 }}>
                Push Notifications
            </Text>
            <Layout
                style={{
                    marginTop: 20,
                    flexDirection: 'row',
                    justifyContent: 'space-between'
                }}
            >
                <Text>Alert Notifications</Text>
                <Toggle
                    onChange={(checked) =>  handleSettingsChange('alertNotifications', checked)}
                    checked={settingsData['alertNotifications']}
                />
            </Layout>


            <Layout
                style={{
                    marginTop: 20,
                    flexDirection: 'row',
                    justifyContent: 'space-between'
                }}
            >
                <Text>Chat Notifications</Text>
                <Toggle
                    onChange={(checked) => handleSettingsChange('chatNotifications', checked)}
                    checked={settingsData['chatNotifications']}
                />
            </Layout>
        </Layout>

        <WrapItems
          childStyle={{ marginVertical: 3 }}
          style={{ alignItems: 'center', marginBottom: 10 }}
        >
          <Button onPress={logOut} status="warning" appearance="outline">
            Log Out
          </Button>
          <Button
            onPress={handleDeleteAccount}
            status="danger"
            appearance="ghost"
          >
            Delete Account
          </Button>
          <Text>
            {Constants?.manifest2?.extra?.expoClient?.version
              ? `Version ${Constants.manifest2.extra.expoClient.version}`
              : ''}
          </Text>
        </WrapItems>
      </Layout>
    </Layout>
  );
}
