import { apiSlice } from './apiSlice';
import { usersApiSlice } from './usersApi';
import { AllSectionsResponse, Class, UserData } from '../../app/types';

export const classPagesApi = apiSlice.injectEndpoints({
  overrideExisting: true,
  endpoints: (builder) => ({
    getAllClasses: builder.query({
      query: ({
        userKey, pageNumber, courseTerm, collegeName
      }) => ({
        url: `/classes/masterClassList/${collegeName}`,
        headers: {
          userKey
        },
        params: {
          pageNumber,
          courseTerm
        }
      }),
      transformResponse: (response: { [masterCourseCode: string]: Class }) => {
        const getRatingFromReviews = (reviews) => {
          if (reviews.length === 0) {
            return null;
          }
          let totalAverage = 0;
          let counter = 0;
          for (const review of reviews) {
            if (review.rating) {
              totalAverage += review.rating;
              counter++;
            }
          }
          if (counter > 0) {
            return (totalAverage / counter).toFixed(2);
          }
          return null;
        };

        const getDifficultyFromReviews = (reviews) => {
          if (reviews.length === 0) {
            return null;
          }
          let totalAverage = 0;
          let counter = 0;
          for (const review of reviews) {
            if (review.difficulty) {
              totalAverage += review.difficulty;
              counter++;
            }
          }
          if (counter > 0) {
            return (totalAverage / counter).toFixed(2);
          }
          return null;
        };

        return Object.fromEntries(
          Object.entries(response).map(([key, value]) => {
            return [
              key,
              {
                ...value,
                reviews: value.reviews.sort(),
                rating: getRatingFromReviews(value.reviews),
                difficulty: getDifficultyFromReviews(value.reviews)
              }
            ];
          })
        );
      }
    }),
    getCurrentSection: builder.query<string, void>({
      query: () => '/randomShit/currentTerm',
      transformResponse: (response: { currentTerm: string }) => response.currentTerm
    }),
    getAllSections: builder.query<AllSectionsResponse, void>({
      query: () => '/randomShit/getAllTerms'
    }),
    addClass: builder.mutation({
      query: ({
        userKey, courseCode, courseTerm, userName
      }) => ({
        url: `/users/${userName}`,
        method: 'PATCH',
        headers: {
          userKey
        },
        body: {
          classesToAdd: [{ courseCode, courseTerm }]
        }
      }),
      async onQueryStarted(
        {
          userKey, courseCode, masterCourseCode, courseTerm
        },
        { dispatch, queryFulfilled }
      ) {
        const patchResult = dispatch(
          usersApiSlice.util.updateQueryData(
            'getUserInfo',
            userKey,
            (draft) => {
              if (!(courseTerm in draft.addedClasses.classes)) {
                draft.addedClasses.classes[courseTerm] = {
                  [masterCourseCode]: [courseCode]
                };
              } else if (
                !(masterCourseCode in draft.addedClasses.classes[courseTerm])
              ) {
                draft.addedClasses.classes[courseTerm][masterCourseCode] = [
                  courseCode
                ];
              } else {
                draft.addedClasses.classes[courseTerm][masterCourseCode].push(
                  courseCode
                );
              }
              draft.addedClasses.length++;
            }
          )
        );

        try {
          await queryFulfilled;
        } catch {
          patchResult.undo();
        }
      }
    }),
    addMultipleClasses: builder.mutation<
      { userData: UserData },
      {
        userKey: string;
        courseTerm: string;
        userName: string;
        courseCodePairs: {
          courseCode: string;
          masterCourseCode: string;
        }[];
      }
    >({
      query: ({
        userKey, courseCodePairs, courseTerm, userName
      }) => ({
        url: `/users/${userName}`,
        method: 'PATCH',
        headers: {
          userKey
        },
        body: {
          classesToAdd: courseCodePairs.map((courseCodePair) => ({
            courseCode: courseCodePair.courseCode,
            courseTerm
          }))
        }
      }),
      async onQueryStarted(
        { userKey, courseCodePairs, courseTerm },
        { dispatch, queryFulfilled }
      ) {
        const patchResult = dispatch(
          usersApiSlice.util.updateQueryData(
            'getUserInfo',
            userKey,
            (draft) => {
              for (const { masterCourseCode, courseCode } of courseCodePairs) {
                if (!(courseTerm in draft.addedClasses.classes)) {
                  draft.addedClasses.classes[courseTerm] = {
                    [masterCourseCode]: [courseCode]
                  };
                } else if (
                  !(masterCourseCode in draft.addedClasses.classes[courseTerm])
                ) {
                  draft.addedClasses.classes[courseTerm][masterCourseCode] = [
                    courseCode
                  ];
                } else {
                  draft.addedClasses.classes[courseTerm][masterCourseCode].push(
                    courseCode
                  );
                }
                draft.addedClasses.length++;
              }
            }
          )
        );

        try {
          await queryFulfilled;
        } catch {
          patchResult.undo();
        }
      }
    }),
    removeClass: builder.mutation({
      query: ({
        userKey, courseCode, courseTerm, userName
      }) => ({
        url: `/users/${userName}`,
        method: 'PATCH',
        headers: {
          userKey
        },
        body: {
          classesToRemove: [{ courseCode, courseTerm }]
        }
      }),
      async onQueryStarted(
        {
          userKey, courseCode, masterCourseCode, courseTerm
        },
        { dispatch, queryFulfilled }
      ) {
        const patchResult = dispatch(
          usersApiSlice.util.updateQueryData(
            'getUserInfo',
            userKey,
            (draft) => {
              draft.addedClasses.classes[courseTerm][masterCourseCode] = draft.addedClasses.classes[courseTerm][masterCourseCode].filter(
                (classCode) => classCode !== courseCode
              );
              // Delete the master course code if there are no more classes
              if (
                draft.addedClasses.classes[courseTerm][masterCourseCode]
                  .length === 0
              ) {
                delete draft.addedClasses.classes[courseTerm][masterCourseCode];
              }
              draft.addedClasses.length--;
            }
          )
        );

        try {
          await queryFulfilled;
        } catch {
          patchResult.undo();
        }
      }
    }),
    isAllowedToReview: builder.query({
      query: ({ masterCourseCode, userKey }) => ({
        url: '/users/isAllowedToReview',
        method: 'GET',
        params: {
          masterCourseCode
        },
        headers: {
          userKey
        }
      })
    }),
    sendReview: builder.mutation({
      query: ({ userKey, body }) => ({
        url: '/classes/userReview',
        method: 'PATCH',
        headers: {
          userKey
        },
        body,
        responseHandler: (response) => response.text()
      })
    })
  })
});

export const {
  useGetAllClassesQuery,
  useGetCurrentSectionQuery,
  useAddClassMutation,
  useAddMultipleClassesMutation,
  useRemoveClassMutation,
  useIsAllowedToReviewQuery,
  useSendReviewMutation,
  useLazyGetAllClassesQuery,
  useLazyGetAllSectionsQuery
} = classPagesApi;
